$sidebar-bg-color: #1d1d1d;
$submenu-bg-color: rgba(255, 255, 255, 0.05);
$submenu-bg-color-collapsed: rgba(29, 29, 29, 0.95);
$sidebar-color: #adadad;
$highlight-color: #d8d8d8;
$icon-bg-color: rgba(255, 255, 255, 0.05);
$icon-size: 35px;

@import '~react-pro-sidebar/dist/scss/styles.scss';
body {
  margin: 0;
  height: 100vh;
  color: #353535;
  font-family: 'Roboto', sans-serif;
}
#root {
  height: 100%;
}
.supply-chain{
    height: 100%;
    display: flex;
    position: relative;
    .header-wrapper{
        max-width: 48px;
    }
    .logo-wrapper{
        border-bottom: 2px solid #63513d;
        .nestle-logo{
            max-width: 320px;
        }
    }
    .btn-toggle {
        cursor: pointer;
        width: 35px;
        height: 35px;
        background: #353535;
        color: #fff;
        text-align: center;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 15px;
        margin-bottom: 14px;
        display: none;
    }
    .sidebar-btn-wrapper {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 14px;
        .sidebar-btn {
            padding: 1px 15px;
            border-radius: 40px;
            background: rgba(255, 255, 255, 0.05);
            color: #adadad;
            text-decoration: none;
            margin: 0 auto;
            height: 35px;
            display: flex;
            align-items: center;
            justify-content: center;
            text-overflow: ellipsis;
            overflow: hidden;
            span {
                margin-left: 5px;
                font-size: 13px;
            }
            &:hover {
                color: $highlight-color;
            }
        }
    }
    .collapsed {
        .sidebar-btn {
            display: inline-block;
            border-radius: 50%;
            width: 35px;
            height: 35px;
            line-height: 40px;
            padding: 0;
        }
    }
    main {
        padding: 24px;
        flex-grow: 1;
        display: flex;
        flex-direction: column;
        overflow-y: auto;
        footer {
            margin-top: auto;
            color: #888;
            text-align: center;
        }
    }
    @media (max-width: 767.98px) {
        overflow-x: hidden;
        header {
            h1 {
                font-size: 24px;
            }
        }
    }
    @media (max-width: $breakpoint-md) {
        .btn-toggle {
            display: flex;
        }
    }
}

.badge {
  padding: 3px 6px;
  font-size: 9px;
  letter-spacing: 1px;
  border-radius: 15px;
  &.red {
    color: #ffffff;
    background: #d63030;
  }
  &.gray {
    color: #ffffff;
    background: #777777;
  }
  &.yellow {
    color: #000000;
    background: #ffd331;
  }
}